@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200..800&display=swap');

:root {
	--primary-bg-color: #fff;
	--primary-color: #58655a;
	--secondary-color: #575756;
}

* {
	margin: 0;
	padding: 0;
	font-family: "Manrope", sans-serif;
	scroll-behavior: smooth;
}

*::-webkit-scrollbar {
	display: none;
}

a {
	color: var(--secondary-color);
	text-decoration: none;
}

ul {
	display: flex;
	justify-content: space-around;
}

li {
	list-style: none;
	color: var(--secondary-color);
}

h1 {
	font-size: 3.8rem;
	font-weight: 500;
}

button {
	outline: none;
	border: none;
	color: white;
}

.primc {
	color: var(--primary-color);
}

svg {
	color: var(--primary-color);
	background-color: transparent;
}

p {
	color: var(--secondary-color);
	width: fit-content;
}

.curve {
	position: relative;
	z-index: 1;
	width: 2rem;
	border: 0.8rem solid white;
	background-color: transparent;
}

/* navbar */
nav {
	backdrop-filter: blur(10px);
	width: 100%;
	height: 4.5rem;
	opacity: 1;
	display: flex;
	justify-content: space-around;
	align-items: center;
	z-index: 5;
	position: sticky;
	top: 0;
}

nav>.left {
	font-size: 1.5rem;
	display: flex;
	align-items: center;
}

nav>.left>svg {
	width: 1.5rem;
	padding: 0.4rem;
	border-radius: 2rem;
	margin-right: 1rem;
	color: white;
	background-color: var(--primary-color);
}

.mid>ul>li {
	margin: 0 1rem;
}

.bkapt {
	background-color: var(--primary-color);
	padding: 1.2rem;
	font-size: 1.1rem;
	height: 3rem;
	border-radius: 0.8rem;
	display: flex;
	align-items: center;
}

.bkapt:hover {
	cursor: pointer;
}

/* navbar */

/* home */
.home {
	width: 75%;
	display: flex;
	margin: auto;
	align-items: center;
}

.home>.left {
	width: 50%;
}

.left>p {
	width: 75%;
	font-size: 1.2rem;
	margin-top: 1rem;
}

.home>.left>.bkapt {
	margin-top: 2rem;
}

.home>.right {
	width: 50%;
}

.right>img {
	width: 100%;
	height: 100%;
	border-radius: 1.4rem;
	position: relative;
	top: -3rem;
}

.right>svg {
	width: 2rem;
	padding: 0.8rem;
	border: 0.8rem solid white;
	border-radius: 3rem;
	position: relative;
	left: 90%;
	z-index: 1;
	color: white;
	background-color: var(--primary-color);
}

.dogbar {
	width: fit-content;
	position: relative;
	bottom: 6.5rem;
	right: 2rem;
	border: 0.8rem solid white;
	border-radius: 3rem;
	background-color: white;
	padding: 0.5rem 1rem;
}

.dogbar>p {
	margin: 0.5rem auto;
}

.dogs {
	display: flex;
}

.dog {
	width: 3rem;
	height: 3rem;
	border-radius: 50%;
	overflow: hidden;
}

.dogs>.dog>img {
	width: 100%;
	height: 100%;
	object-position: center;
	object-fit: cover;
}

/* home */

/* about */
#about {
	width: 100%;
	overflow: hidden;
	height: 80vh;
	padding: 6rem 0;
}

.abt-cont {
	width: 75%;
	height: 70vh;
	margin: auto;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.bg-img {
	width: 100%;
	position: relative;
	z-index: -1;
	bottom: 100%;
	opacity: 0.2;
}

.tag {
	align-items: center;
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	gap: 4px;
	height: min-content;
	overflow: hidden;
	position: relative;
	font-size: 0.7rem;
	padding: 0.3rem;
	border-radius: 0.5rem;
	width: max-content;
	margin-bottom: 2rem;
	border: 1px solid rgba(89, 94, 102, .15);
	box-shadow: rgba(212, 212, 212, 0.035) 0px 0.361312px 0.650362px -0.166667px inset,
		rgba(212, 212, 212, 0.07) 0px 1.37312px 2.47162px -0.333333px inset,
		rgba(212, 212, 212, 0.224) 0px 6px 10.8px -0.5px inset;
}



.tag>p {
	outline: none;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	transform: none;
	opacity: 1;
}

.lt,
.rt {
	width: 40%;
}

.lt>h1 {
	margin-bottom: 2rem;
}

.rt {
	display: flex;
	flex-wrap: wrap;
	width: 45%;
}

.expcrd {
	width: 50%;
	margin-bottom: 2rem;
}


.testimonial {
	width: 100%;
}

.testi-cont {
	width: 75%;
	margin: 5rem auto;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.l,
.r {
	width: 40%;
}

.l>img {
	width: 80%;
	height: 60vh;
	border: 0.8rem solid #ffffff80;
	box-shadow: 0 0.375rem 0.75rem #00000014;
	border-radius: 2rem;
	object-fit: cover;
}

.r>h2 {
	text-align: justify;
	font-weight: 500;
}

.strp {
	width: min-content;
	display: flex;
	margin: 2rem 0;
	gap: 0.5rem;
}

.cutting {
	width: 100%;
	overflow: hidden;
	height: 150vh;
}

.cut-cont {
	width: 60%;
	margin: 5rem auto;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.cut-cont>* {
	text-align: center;
	margin-bottom: 2rem;
}

.cut-cont>h1 {
	font-size: 3.8rem;
	font-weight: 500;
}

.cut-cont>video {
	width: 100%;
	outline: none;
	border: 0.8rem solid #ffffff80;
	box-shadow: 0 0.375rem 0.75rem #00000014;
	border-radius: 2rem;
}

/* about */

/* services */
#services {
	padding: 6rem 0;
}

.serv-cont {
	width: 75%;
	margin: auto;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.serv-cont>* {
	text-align: center;
	margin-bottom: 2rem;
}

.servtabs {
	width: 100%;
}

.servtbbtn {
	display: flex;
	background-color: rgba(87, 87, 87, .04);
	border-radius: 0.5rem;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	height: 4.5rem;
	margin-bottom: 0.5rem;
	padding: 0 0.4rem;
}

.servbtn {
	display: flex;
	width: 30%;
	height: 3.5rem;
	justify-content: center;
	align-items: center;
	gap: 0.5rem;
	cursor: pointer;
}

.sactivebtn {
	border: 1px solid rgba(89, 94, 102, .15);
	background-color: white;
	border-radius: 0.375rem;
	box-shadow: rgba(212, 212, 212, 0.035) 0px 0.361312px 0.650362px -0.166667px inset, rgba(212, 212, 212, 0.07) 0px 1.37312px 2.47162px -0.333333px inset, rgba(212, 212, 212, 0.224) 0px 6px 10.8px -0.5px inset;
}

.servbtn>svg {
	height: 2rem;
}

.servtbcont {
	width: 100%;
	height: 60vh;
	overflow: hidden;
	padding: 0 0.4rem;
}

.servcont {
	display: flex!important;
	width: 100%;
	gap: 1rem;
	justify-content: center;
	align-items: center;
}
.sactivecont{
	display: flex;
}

.sl {
	width: 45%;
	border-radius: 0.375rem;
	object-fit: cover;
	object-position: center;
}

.sr {
	padding: 0 2rem;
	display: flex;
	flex-direction: column;
	justify-content: center;
	width: 65%;
	border: 1px solid rgba(89, 94, 102, .15);
	background-color: white;
	border-radius: 0.375rem;
	box-shadow: rgba(212, 212, 212, 0.035) 0px 0.361312px 0.650362px -0.166667px inset, rgba(212, 212, 212, 0.07) 0px 1.37312px 2.47162px -0.333333px inset, rgba(212, 212, 212, 0.224) 0px 6px 10.8px -0.5px inset;
}

.sr>* {
	margin: 1rem 0;
}

.sr>h1 {
	font-size: 2rem;
	width: fit-content;
}

.sr>ul {
	flex-direction: column;
	gap: 0.5rem;
}

.sr>ul>li {
	display: flex;
	gap: 0.5rem;
}

.sr>.bkapt {
	padding: 0.4rem;
	width: fit-content;
	height: 2rem;
	border-radius: 0.5rem;
}

/* services */

.footer {
	height: 10rem;
}